import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { ChecklistFormService } from "../services/checklistformService";
import swal from 'sweetalert2';
import { MatDatepickerModule, MatNativeDateModule, DateAdapter } from '@angular/material';
import { DatePipe } from '@angular/common'

declare interface DataTable {
    chklistFormHeaderRow: string[];
    chklistFormFooterRow: string[];
    chklistFormDataRows: any;
}

declare const $: any;

@Component({
    selector: 'app-checklist-form',
    templateUrl: './checklist-form.component.html',
    styleUrls: ['./checklist-form.component.scss'],
    providers: [CommonService, ChecklistFormService]
})
export class ChecklistFormComponent implements OnInit {
    public checklistFormDataTable: DataTable;
    public checklistFormList: any;
    public userProfile: any;

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private checklistService: ChecklistFormService,
        private activatedRoute: ActivatedRoute
    ) { }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {

            this.getChecklistForms();

            this.checklistFormDataTable = {
                chklistFormHeaderRow: ['#', 'Form Name', 'Form Description', 'Last Updated', 'Updated By', 'Status'],
                chklistFormFooterRow: ['#', 'Form Name', 'Form Description', 'Last Updated', 'Updated By', 'Status'],
                chklistFormDataRows: this.checklistFormList
            };
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }
    
    getChecklistForms() {

        $('.loader').show();

        this.checklistService.GetCheckListFormList(this.commonService.baseApiUrl).subscribe(
            data => {
                var datachklistforms = JSON.stringify(data);
                var chklistformParse = JSON.parse(datachklistforms);
                var chklistformList = JSON.parse(chklistformParse["_body"]);
                localStorage.removeItem("chklistformList");
                this.checklistFormList = null;
                if (chklistformList != null) {
                    $("#chklistFormDataTable").dataTable().fnDestroy();
                    localStorage.setItem("chklistformList", JSON.stringify(chklistformList));
                    this.checklistFormList = chklistformList;
                    console.log(this.checklistFormList);
                    this.checklistFormDataTable = {
                        chklistFormHeaderRow: ['#', 'Form Name', 'Form Description', 'Last Updated', 'Updated By', 'Status'],
                        chklistFormFooterRow: ['#', 'Form Name', 'Form Description', 'Last Updated', 'Updated By', 'Status'],
                        chklistFormDataRows: this.checklistFormList
                    };

                    setTimeout(() => {
                        $("#chklistFormDataTable").dataTable().fnDestroy();
                        $('#chklistFormDataTable').DataTable({
                            'pagingType': 'full_numbers',
                            'lengthMenu': [[10, 25, 50, -1], [10, 25, 50, 'All']],
                            responsive: true,
                            language: {
                                search: '_INPUT_',
                                searchPlaceholder: 'Search records',
                            }
                        });

                        const tablechklistform = $('#chklistFormDataTable').DataTable();

                        // Edit record
                        tablechklistform.on('click', '.edit', function () {

                        });

                        // Delete a record
                        tablechklistform.on('click', '.remove', function (e: any) {
                            const $trTT = $(this).closest('tr');
                            tablechklistform.row($trTT).remove().draw();
                            e.preventDefault();
                        });

                         //Like record
                        tablechklistform.on('click', '.like', function () {
                            alert('You clicked on Like button');
                        });

                    }, 10);

                    $('.loader').hide();
                }
            }
        );
    }

    goToCreateChecklistForm(chkfomrid: any) {
        this.router.navigate(['create-checklistform'], { queryParams: { chkfomrid: chkfomrid } });
    }
}



