import { Routes } from '@angular/router';
import { ChecklistFormComponent } from './checklist-form.component';

export const ChecklistFormRoutes: Routes = [
    {
        path: '',
        children: [{
            path: '',
            component: ChecklistFormComponent
        }]
    }
];


